:root {
    --font: 'Montserrat', sans-serif;
    --color: #fff;
    --colorSecond: #6a6a6aa8;
    --colorOpacity: #ffffffe0;
    --colorInvert: #000;
    --colorWhite: #fff;
    --colorBlack: #000;
    --helpColor: #c4c3c6; 
    --bgBody: #101010;
    --bgSecond: #181818;
    --bgSecondHelper: rgba(0, 0, 0, 0.75);
}

@media (prefers-color-scheme: light) {
    :root {
        --font: 'Montserrat', sans-serif;
        --color: #000;
        --colorSecond: #c3c3c3;
        --colorOpacity: #000000e0;
        --colorInvert: #fff;
        --colorWhite: #fff;
        --colorBlack: #000;
        --helpColor: #a1a1a1; 
        --bgBody: #fff;
        --bgSecond: #f9f9f9;
        --bgSecondHelper: rgba(0, 0, 0, 0.06);
    }
}

