.bg-js {
    background-color: #bba500 !important;
}
.bg-ts {
    background-color: #2f74c0 !important;
}
.bg-node {
    background-color: #74ab5f !important;
}
.bg-react {
    background-color: #33afd1 !important;
}

.title-color {
    color: #12c9f5 !important;
}

.button-style {
    background-color: var(--bgColor) !important;
    border: 3px solid var(--color)!important;
    border-radius: 0!important;
    color: var(--color)!important;
    font-size: 24px!important;
    transition: all .15s ease;
}
.button-style:hover {
    color: #000 !important;
    background-color: var(--color)!important;
}